.home-title {
  color: #fff;
  font-family: "Titillium Web", Helvetica, sans-serif;
  font-size: 36px;
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 1;
  margin: 0; }
  .home-title:hover {
    color: #4aedc4; }
  @media (min-width: 600px) {
    .home-title {
      font-size: 45px;
      letter-spacing: 2px;
      line-height: 1; } }
  @media (min-width: 950px) {
    .home-title {
      font-size: 55px;
      line-height: 58px; } }

.title-wrapper .third-line {
  font-size: 29.16px;
  line-height: 1.5; }
  @media (min-width: 600px) {
    .title-wrapper .third-line {
      font-size: 40.5px; } }
  @media (min-width: 950px) {
    .title-wrapper .third-line {
      font-size: 49.5px; } }

.subheading {
  color: #ffeb3b;
  font-family: "OpenSans-Light", Helvetica, sans-serif;
  font-size: 12px;
  letter-spacing: 1.1px; }
  @media (min-width: 600px) {
    .subheading {
      font-family: "OpenSans-SemiBold", Helvetica, sans-serif;
      font-size: 16px;
      font-weight: 500;
      letter-spacing: 2px; } }

.title {
  color: #4aedc4;
  font-family: "Titillium Web", Helvetica, sans-serif;
  font-size: 40px;
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 1;
  margin: 0;
  position: absolute;
  z-index: 1; }
  @media (min-width: 600px) {
    .title {
      font-size: 45px;
      font-weight: 600; } }
  @media (min-width: 950px) {
    .title {
      line-height: 1.1; } }

.titlewrapper {
  padding: 1rem 0; }

/* 
 * Header 
 */
.header-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 55px;
  margin-top: 3rem; }
  @media (min-width: 600px) {
    .header-wrapper {
      height: 60px;
      width: 94vw; } }

.greybg {
  border-top: 2px solid #14a37f;
  border-bottom: 2px solid #14a37f;
  position: relative;
  width: 100vw; }
  @media (min-width: 600px) {
    .greybg {
      width: 94vw; } }

@media (min-width: 950px) {
  .greybg-mob {
    display: none; } }

.greybg-desk {
  display: none; }
  @media (min-width: 950px) {
    .greybg-desk {
      display: block; } }

.home-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 6%;
  height: 100vh; }
  @media (min-width: 600px) {
    .home-container {
      background-size: contain;
      padding: 0 10%; } }

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0 10%; }

.about {
  display: flex;
  padding: 0 1.5rem; }
  @media (min-width: 600px) {
    .about {
      padding: 0 2rem; } }
  @media (min-width: 950px) {
    .about {
      flex-direction: column;
      justify-content: space-evenly;
      align-items: flex-start; } }

.skills {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }

.project-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 5rem; }

.form-container {
  width: 100%; }

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

/* Title */
.reverse {
  color: #4aedc4; }
  .reverse:hover {
    color: #fff; }

.J {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 55px;
  font-weight: 600; }

.row {
  display: flex;
  align-items: flex-end;
  margin: auto; }

/** Button */
.btn-wrapper {
  margin-top: 4rem;
  font-family: "OpenSans-Light", Helvetica, sans-serif; }
  @media (min-width: 600px) {
    .btn-wrapper {
      margin-top: 5rem; } }

.contact-btn {
  border: 1px solid #4aedc4;
  border-radius: 3px;
  color: #14a37f;
  font-family: "OpenSans-Light", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 550;
  padding: 8px 12px;
  letter-spacing: 1px;
  text-transform: uppercase; }
  .contact-btn:hover {
    background: #4aedc4;
    color: #37393b; }
  @media (min-width: 600px) {
    .contact-btn {
      font-size: 14px; } }
